const fingerScan = (() => {
  let calibrationTime = 10000;
  let scanTime = 60000;
  let strictMode = false;
  let isScanning = false;
  let frameID = 0;
  let start_time = 0;
  let noDetectionCount = 0;
  let wakeLock = undefined;
  let video;
  let canvas;
  let ctx;
  let raw_intensity = [];
  let ppg_time = [];
  let onFrameCallback = ({ timeElapsed = 0, type = "", percentage = 0, isFingerInView = false }) => {};
  let onScanFinishCallback = ({ raw_intensity = [], ppg_time = [], average_fps = 0 }) => {};
  let onErrorCallback = (err = new Error("Fingerscan Error.")) => {};

  const setupCamera = () =>
    new Promise(async (resolve, reject) => {
      try {
        const isIOS = /iPhone|iPad|iPod|Macintosh|Mac/i.test(navigator.userAgent);
        const stream = await navigator.mediaDevices.getUserMedia({
          audio: false,
          video: {
            facingMode: "environment",
            aspectRatio: 16 / 9,
            frameRate: { ideal: 60 },
            height: isIOS ? { exact: 720 } : undefined,
          },
        });
        stream
          .getVideoTracks()?.[0]
          ?.applyConstraints?.({ advanced: [{ torch: true }] })
          .catch((err) => console.error(new Error("Flash could not be acquired.", { cause: err })));
        video.srcObject = stream;
        video.onloadedmetadata = () => {
          resolve();
        };
      } catch (err) {
        reject(new Error("We are not able to access the Camera. Please try again.", { cause: err }));
      }
    });

  const stopScan = (noCallback = false) => {
    cancelAnimationFrame(frameID);
    video?.srcObject?.getTracks?.()?.forEach?.((track) => {
      track?.stop?.();
    });
    isScanning = false;
    window.onblur = undefined;
    wakeLock
      ?.release()
      .then(() => console.log("WakeLock Released."))
      .catch((err) => {
        console.log("WakeLock Error.");
        console.error(err);
      });
    if (!noCallback)
      onScanFinishCallback({
        raw_intensity,
        ppg_time,
        average_fps: Math.round((raw_intensity.length * 1000) / scanTime),
      });
  };

  const calcQuality = (rgb = { r: 0, g: 0, b: 0 }) => {
    let confidence = undefined;
    if (rgb.r < (strictMode ? 25 : 5)) confidence = 0;
    else {
      const nonRed = rgb.g + rgb.b;
      const min = strictMode ? 35 : 60;
      const max = strictMode ? 125 : 150;
      if (nonRed <= min) confidence = 1;
      else if (nonRed >= max) confidence = 0;
      else confidence = (max - nonRed) / min;
    }
    return confidence;
  };

  const calcRGB_fromImageData = (imgData) => {
    let count = 0,
      sumRGB = { r: 0, g: 0, b: 0 };
    for (let i = 0; i < imgData.data.length; i += 4)
      if (imgData.data[i + 3] > 0) {
        count++;
        sumRGB.r += imgData.data[i];
        sumRGB.g += imgData.data[i + 1];
        sumRGB.b += imgData.data[i + 2];
      }
    return { r: sumRGB.r / count, g: sumRGB.g / count, b: sumRGB.b / count };
  };

  const drawCanvas = () => {
    const imageWidth = video.videoWidth;
    const imageHeight = video.videoHeight;
    ctx.save();
    ctx.clearRect(0, 0, imageWidth, imageHeight);
    ctx.drawImage(video, 0, 0, imageWidth, imageHeight);
    const avgRGB = calcRGB_fromImageData(ctx.getImageData(imageWidth / 5, imageHeight / 5, imageWidth * 0.6, imageHeight * 0.6));
    ctx.restore();
    return avgRGB;
  };

  const scan = async (loop_start_time) => {
    const timeElapsed = loop_start_time - start_time;
    try {
      if (timeElapsed <= calibrationTime) {
        const intensity = drawCanvas();
        const quality = calcQuality(intensity);
        onFrameCallback({
          timeElapsed,
          type: "calibration",
          percentage: Math.round((timeElapsed / calibrationTime) * 100),
          isFingerInView: quality > 0.6,
        });
        frameID = requestAnimationFrame(scan);
      } else if (timeElapsed <= calibrationTime + scanTime) {
        const intensity = drawCanvas();
        const quality = calcQuality(intensity);
        if (quality > 0.6) noDetectionCount = 0;
        else {
          if (strictMode) noDetectionCount = 101;
          else noDetectionCount++;
        }
        if (noDetectionCount > 100) {
          stopScan(true);
          onErrorCallback(new Error("Finger movement detected during the scan."));
          return;
        }
        raw_intensity.push(intensity);
        ppg_time.push(performance.now() - start_time);
        onFrameCallback({
          timeElapsed,
          type: "scan",
          percentage: Math.round(((timeElapsed - calibrationTime) / scanTime) * 100),
          isFingerInView: quality > 0.6,
        });
        frameID = requestAnimationFrame(scan);
      } else stopScan();
    } catch (err) {
      stopScan(true);
      onErrorCallback(new Error("Sorry we're unable to compute the signal. Please try again.", { cause: err }));
    }
  };

  const startScan = async ({
    calibrationTime_inSec = 10,
    scanTime_inSec = 60,
    strict_mode = false,
    videoElement,
    canvasElement,
  }) => {
    raw_intensity = [];
    ppg_time = [];
    isScanning = false;
    noDetectionCount = 0;

    try {
      if (typeof calibrationTime_inSec !== "number" || calibrationTime_inSec < 3 || calibrationTime_inSec > 20)
        throw new Error("Calibration duration can be between 3-20 seconds.");
      if (typeof scanTime_inSec !== "number" || scanTime_inSec < 10 || scanTime_inSec > 120 || scanTime_inSec % 5 !== 0)
        throw new Error("Scan duration can be between 10-120 seconds (multiple of 5).");
      if (calibrationTime_inSec > scanTime_inSec) throw new Error("Scan duration cannot be smaller than Calibration duration.");
      calibrationTime = calibrationTime_inSec * 1000;
      scanTime = scanTime_inSec * 1000;
      if (typeof strict_mode !== "boolean") {
        console.error("Provided Strict Mode value is Invalid!\nSwitching to default...");
        strictMode = false;
      } else strictMode = strict_mode;

      navigator.wakeLock
        ?.request("screen")
        .then((wakeLockSentinel) => {
          wakeLock = wakeLockSentinel;
          console.log("WakeLock Active.");
        })
        .catch((err) => {
          console.log("WakeLock Error.");
          console.error(err);
        });

      video = typeof videoElement === "undefined" ? document.getElementById("videoInput") : videoElement;
      if (video) {
        await setupCamera();
        await video.play?.();
      } else throw new Error("Cannot get the video element.");

      canvas = typeof canvasElement === "undefined" ? document.getElementById("canvasOutput") : canvasElement;
      if (canvas) {
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        ctx = canvas.getContext("2d");
      } else throw new Error("Cannot get the canvas element.");

      start_time = performance.now();
      frameID = requestAnimationFrame(scan);
      isScanning = true;
      window.onblur = () => {
        stopScan(true);
        onErrorCallback(
          new Error("App functionality disabled in the Background. Keep it in the Foreground for proper operation.")
        );
      };
    } catch (err) {
      stopScan(true);
      onErrorCallback(err);
      throw new Error("Fingerscan Initialization Error.", { cause: err });
    }
  };

  return {
    startScan,
    stopScan,
    onFrame: (callback = ({ timeElapsed = 0, type = "", percentage = 0, isFingerInView = false }) => {}) => {
      if (typeof callback === "function") onFrameCallback = callback;
    },
    onScanFinish: (callback = ({ raw_intensity = [], ppg_time = [], average_fps = 0 }) => {}) => {
      if (typeof callback === "function") onScanFinishCallback = callback;
    },
    onError: (callback = (err = new Error("Fingerscan Error.")) => {}) => {
      if (typeof callback === "function") onErrorCallback = callback;
    },
    get isInitializing() {
      return () => !Boolean(isScanning);
    },
    get isScanning() {
      return () => Boolean(isScanning);
    },
  };
})();

export default fingerScan;
